:root {
    --bar-min-width: 600px;
    --bar-max-width: 90vw;
    --bar-width: 30vw;
    --bar-width-expanded: 60vw;
    --bar-background: rgba(0, 0, 0, 0.8);
    --footer-background: rgba(0, 0, 0, 0.8);
    --color-shadow: rgba(255, 255, 255, 0.7);
    --color-light-text: rgba(255, 255, 255, 0.6);
    --color-light-text-hover: rgba(255, 255, 255, 0.8);
    --color-icon-bg: rgba(255, 255, 255, 0.2);
    --color-icon-bg-hover: rgba(255, 255, 255, 0.4);
    --color-border: rgba(255, 255, 255, 0.2);
    --color-item-highlighted: rgba(255, 255, 255, 0.15);
    --color-item-hover: rgba(255, 255, 255, 0.05);
    --border-radius-bar: 0.6rem;
    --border-radius-item: 0.4rem;
    --icon-size: 1.125em;

    /* Neos UI colors */
    --grayLight: #3f3f3f;
    --grayLighter: #eee;
    --grayMedium: #323232;
    --textSubtleLight: #adadad;
    --textOnGray: #fff;
    --textOnWhite: #252525;
    --primaryBlue: #00adee;
}

.CommandBar-module__commandBar___3t_V0 {
    backdrop-filter: blur(10px);
    background: var(--bar-background);
    border-radius: var(--border-radius-bar, 5);
    border: 1px solid var(--color-border);
    box-shadow: 0 3px 10px rgba(0, 0, 0, 0.5);
    color: white;
    display: flex;
    flex-direction: column;
    left: 50%;
    margin: 0;
    max-width: var(--bar-max-width);
    min-width: var(--bar-min-width);
    overflow: hidden;
    position: absolute;
    padding: 0;
    top: 50%;
    translate: -50% -50%;
    width: var(--bar-width);
    pointer-events: all;
    transition: width 0.1s ease-out, opacity 0.05s;
}

.CommandBar-module__commandBar___3t_V0.CommandBar-module__hasResults___3NzEf {
    width: var(--bar-width-expanded);
}

.CommandBar-module__commandBar___3t_V0 small {
    color: var(--color-light-text);
